import React,{Component} from 'react';

import DesarrolloEconomico from './DesarrolloEconomico';
import FuncionPublica from './FuncionPublica';


import './NavTab.css';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import axios from 'axios';

//CARDS
import Card from 'react-bootstrap/Card';

import Administradores from './Administradores';
import Desarrolladpres from './Desarrolladpres';

const urlprop="54.235.229.77:3100";
class NavTab extends Component{
   
   
    constructor(props){
        super(props)
        this.state = {
          selectOptions : [],
          selectOptionsDesAdm : [],
          id: "",
          name: '',
          desc:'',
          rol:''
        }
      }
    
     async getOptions(){
        const res = await axios.get('http://'+urlprop+'/listImgs')
        const data = res.data.data
    
        

        const optionsDesAdm = data.map(d => ({
            "id" : d._id,
            "nombre" : d.nombre,
            "url": d.url
      
          }))
    
        this.setState({selectOptionsDesAdm: optionsDesAdm});
        console.log(this.state.selectOptionsDesAdm);
    
      }
    
      handleChange(e){
      
      }
    
      componentDidMount(){
          this.getOptions()
      }
   


    render(){

        return (
            <div>
                <Tabs
              defaultActiveKey="profile"
              id="uncontrolled-tab-example"
              className="mb-3"
              fill
            >
              <Tab eventKey="home" title="Home">
                    <div className='container'>
                    
                    <div className='wrapper'>
                    
                       {this.state.selectOptionsDesAdm.map((variant) => (
                                   
                                   <div className='item'>
                                       
                                       <Card
                                           bg='dark'
                                           key={variant.nombre}
                                           text={ 'white'}
                                           >
                                           <Card.Body>
                                           <img  src={variant.url}  width="700" height="400"/>
                                           </Card.Body>
                                       </Card>
                                   </div>
                               
                           ))}
                      </div>
                    </div>
                                
                        
              </Tab>
              <Tab eventKey="Administradores" title="Administradores">
                
                <>
                <Administradores/>
                </>
                
              </Tab>
              <Tab eventKey="Desarrolladores" title="Desarrolladores" >
                <>
                <Desarrolladpres/>
                    </>
              </Tab>

              <Tab eventKey="FuncionPublica" title="Funcion Publica" >
                                <FuncionPublica/>
              </Tab>

              <Tab eventKey="DesarrolloEconomico" title="Desarrollo Economico" >
                                <DesarrolloEconomico/>
              </Tab>

              
            </Tabs>
            </div>
          );
    }
}


export default NavTab;