import React,{Component} from 'react';

import './Departamentos.css';
import axios from 'axios';

//CARDS
import Card from 'react-bootstrap/Card';

var optionsCanv;
const urlprop="54.235.229.77:3100";
// const urlprop="localhost:3100";
class Desarrolladpres extends Component{
   
    constructor(props){
        super(props)
        this.state = {
            selectOptions : [],
            selectOptionsDesAdm : [],
            id: "",
            name: '',
            desc:'',
            rol:''
          }
      }
    
      async getOptions(){
        console.log('http://'+urlprop+'/listDevs')
        const res = await axios.get('http://'+urlprop+'/listDevs')
        const data = res.data.data
        console.log(res)


        const optionsDesAdm = data.map(d => ({
            "value" : d._id,
            "name" : d.name,
            "carnet": d.carnet,
            "curso":d.curso,
            "rol": d.rol,
            "img":d.imagen
      
          }))
    
          console.log(optionsDesAdm)
        this.setState({selectOptionsDesAdm: optionsDesAdm})
    
      }
    
      handleChange(e){

      }
    
      componentDidMount(){
          this.getOptions()  
      }


    render(){

        return (

            <div className='container'>
                    <div className='row'>
                            {this.state.selectOptionsDesAdm.map((variant) => (
                                
                                <div className='col-md-3 ml-md-auto'>
                                    <Card
                                        bg='dark'
                                        key={variant.value}
                                        text={ 'white'}
                                        style={{ width: '18rem' }}
                                        className="mb-2"
                                        >
                                        <Card.Header>{variant.rol}</Card.Header>
                                        <img  src={variant.img} width="285" height="300"/>
                                        <Card.Body>
                                            <Card.Title>{variant.name}  </Card.Title>
                                            <Card.Text>
                                            {variant.carnet}
                                            <br></br>
                                            {variant.curso}
                                            </Card.Text>
                                        </Card.Body>
                                    </Card>
                                </div>
                            
                        ))}
                    </div>
                </div>
          );
    }
}


export default Desarrolladpres;