import React,{Component} from 'react';

import './FuncionPublica.css';
import axios from 'axios';

//CARDS
import Card from 'react-bootstrap/Card';

const urDom="54.235.229.77:3100";
class FuncionPublica extends Component{
 

   
    constructor(props){
        super(props)
        this.state = {
          selectOptionsDesAdm : [],
          selectOptionsNotice : [],

        }
      }
    
     async getOptions(){
        
        const res = await axios.get('http://'+urDom+'/listFunctions')
        const data = res.data.data
    
        console.log("pruebas1")
        console.log(res)
        const optionsDesAdm = data.map(d => ({
            "id": d._id,
            "nombre" : d.nombre,
            "descripcion" : d.descripcion,
            "imagen": d.imagen
      
          }))


          const resNot = await axios.get('http://'+urDom+'/listNews')
        const dataNot = resNot.data.data
    
        console.log("pruebas1")
        console.log(resNot)
        const optionsNot = dataNot.map(d => ({
            "id": d._id,
            "nombre" : d.nombre,
            "descripcion" : d.descripcion
      
          }))
    

        this.setState({selectOptionsDesAdm: optionsDesAdm})
        this.setState({selectOptionsNotice: optionsNot})
    
      }
    
      handleChange(e){
       
      }
    
      componentDidMount(){
          this.getOptions()            
      }


    render(){

        

      

        return (



            <div className='container'>

                <div className='container' id='FP'>
                  {/* funcion publica inicio*/}
                  <>
                <nav>
                        <h2>Funcion Publica</h2>
                    </nav>
                <div className='container'>
                    <div className='row'>
                            {this.state.selectOptionsDesAdm.map((variant) => (
                                
                                <div className='col-md-3 ml-md-auto'>
                                    <Card
                                        bg='dark'
                                        key={variant.id}
                                        text={ 'white'}
                                        style={{ width: '18rem' }}
                                        className="mb-2"
                                        
                                        >
                                        <Card.Header>{variant.nombre}</Card.Header>
                                        <img  src={variant.imagen} width="285" height="300"/>
                                        <Card.Body>
                                            <Card.Title>{variant.nombre}  </Card.Title>
                                            <Card.Text>
                                            {variant.descripcion}
                                            </Card.Text>
                                        </Card.Body>
                                    </Card>
                                </div>
                            
                        ))}
                    </div>
                </div>
                    
                    </>

                <div className='container'>
                    <nav>
                        <h3>ULTIMAS NOTICIAS</h3>
                    </nav>
                    <div className='wrapper'>
                    {/* selectOptionsNotice */}
                       {this.state.selectOptionsNotice.map((variant) => (
                                   
                                   <div className='item'>
                                       
                                       <Card
                                           bg='dark'
                                           key={variant.id}
                                           text={ 'white'}
                                           style={{ width: '18rem' }}
                                           className="mb-2"
                                           
                                           >
                                           <Card.Header>{variant.nombre}</Card.Header>
                                           
                                           <Card.Body>
                                               <Card.Title ><hr/>  </Card.Title>
                                               <Card.Text>
                                               {variant.descripcion}
                                               </Card.Text>
                                           </Card.Body>
                                       </Card>
                                   </div>
                               
                           ))}
                      </div>
                    </div>

                {/* funcion publica fin     */}
                </div>


                
            </div>
          );
    }
}


export default FuncionPublica;