import React,{Component} from 'react';

import './Departamentos.css';
import CanvasJSReact from "../canvasjs-3.7.1/canvasjs.react";

var optionsCanv;

const urlprop="54.235.229.77:3100";
class DesarrolloEconomico extends Component{
 

   
    constructor(props){
        super(props)
        this.state = {
          optionsCanv :{},
          optionsCanvLN :{},

        }
      }
    
     async getOptions(){

    
      }
    
      handleChange(e){

      }
    
      componentDidMount(){
          this.getOptions()
          //-----------------------------barra
          
          //fetch("http://"+urlprop+"/listGraphs"
          //fetch("http://balanceadorgrupo10-1830538571.us-east-2.elb.amazonaws.com/Atravez_Tiempo/"
          console.log("http://"+urlprop+"/listGraphs")
          fetch("http://"+urlprop+"/listGraphs", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        },
      })
      .then((response) => response.json())
      .then((json) => {
        return json;
      })
      .then((json) => {

        console.log("INICIO")
        var newdatabarra=[]; 
        
         for (var i=0 ; i<json.Atravez_Tiempo[0].length;i++){
         newdatabarra.push({label:json.Atravez_Tiempo[0][i].mes, y:parseInt(json.Atravez_Tiempo[0][i].cantidad),})
         
         }
         console.log(newdatabarra)
         console.log("FIN")
         this.state.optionsCanv = {
          animationEnabled: true,
          exportEnabled: true,
          theme: "light2", //"light1", "dark1", "dark2"
          title: {
            text: "Desarrollo a travez del tiempo.",
          },
          axisY: {
            includeZero: true,
          },
          axisX:{
            includeZero: true,
          },
          data: [
            {
              type: "column", 
              indexLabel: "{y}", 
              indexLabelFontColor: "#5A5757",
              indexLabelPlacement: "outside",
              dataPoints: newdatabarra,
            }
          ],
        };
     
      })
      .catch((error) => {
        console.error(error);
      });
   //fin barras

//-----------------------------lineas
//fetch("http://"+urlprop+"/listGraphs2/"
//fetch("http://balanceadorgrupo10-1830538571.us-east-2.elb.amazonaws.com/DesarrolloEconomico/"
console.log("http://"+urlprop+"/listGraphs2")
fetch("http://"+urlprop+"/listGraphs2", {
method: "GET",
headers: {
  Accept: "application/json",
  "Content-Type": "application/json",
  },
})
.then((response) => response.json())
.then((json) => {
  return json;
})
.then((json) => {


  var newAxisY=[];
  var newData=[];

  for (var i=0 ; i<json.DesarrolloEconomico[0].length;i++){
    var colorhexa= '#'+(Math.random() * 0xFFFFFF << 0).toString(16).padStart(6, '0');
    var newdataline=[];
    newAxisY.push({ title: json.DesarrolloEconomico[0][i].nombre,includeZero: true,suffix: "k", titleFontColor: colorhexa  });
    
   
    console.log("tamao de cantidad: " + json.DesarrolloEconomico[0][i].cantidad.length)
    for(var j=0; j<json.DesarrolloEconomico[0][i].cantidad.length;j++){
       newdataline.push({x:json.DesarrolloEconomico[0][i].ao[j],y:json.DesarrolloEconomico[0][i].cantidad[j]});
    }
   
    newData.push({ type: "spline", name: json.DesarrolloEconomico[0][i].nombre, color:colorhexa , showInLegend: true,dataPoints:newdataline})
  }


   this.state.optionsCanvLN = {
    animationEnabled: true,
    exportEnabled: true,
    theme: "light2", //"light1", "dark1", "dark2"
    title:{
      text: "titulo lineas"
    },
    axisY:newAxisY,

    toolTip: {
      shared: true
    },
    legend: {
      cursor: "pointer",
    },
    data: newData,
  };

})
.catch((error) => {
  console.error(error);
});
//fin lineas
                       
      }


    render(){

        

      var CanvasJSChart = CanvasJSReact.CanvasJSChart;
      

        return (



            <div className='container'>
                {/* Desarrollo economico inicio     */}
                <div className='container' id='DE' >
                  <nav>
                        <h2>DESARROLLO ECONOMICO</h2>
                    </nav>
                  <div className='container' id='GrapBar'>
                  <CanvasJSChart options={this.state.optionsCanv} />
                  </div>
                      
                  <div className='container' id='GrapLn'>
                      <CanvasJSChart options={this.state.optionsCanvLN} />
                  </div>
                </div>
                {/* Desarrollo economico fin     */}
                
            </div>
          );
    }
}


export default DesarrolloEconomico;